/****************************
 * Main Source Style
 ****************************/

@import "variables";
@import "mixins";
@import "utilities";

.html {
  font-size: 100%;
}

body {
  padding: 0;
  margin: 0;
}

header {
  background: $header-def-bg;
  border-bottom: 1px solid $header-def-border;
  height: 50px;
  .logo {
    font-family: "Open Sans",sans-serif;
    @include fontSize(1.8,rem);
    padding: 0;
    margin: 0;
  }
}

nav {
  padding: 0;
  margin: 0;
  ul {
    list-style-type: none;
    padding: 0;
    text-align: right;
    li {
      display: inline-block;
      margin-right: 10px;
      &:last-of-type {
        margin-right: 0;
      }
      a {
        font-weight: 700;
        text-decoration: none;
      }
    }
  }
}

.big-background-text {
  background: url('../img/header.jpg') fixed no-repeat center;
  background-size: cover;
  .bg-overlay {
    height: 700px;
    background: transparentize(white,0.5);
    @include fontSize(1.4,rem);
  }
}